import styled from 'styled-components';
import Loader from '../Components/Loader';
import PropTypes from 'prop-types';

const Container = styled.div`
  width: 100%;
  font-size: 20px;
`;
const Li = styled.li`
  text-decoration: none;
  list-style: none;
  margin: 20px 5px;
  font-size: 30px;
  font-weight: bold;
`;
const Div = styled.div`
  margin: 10px 20px;
`;
function ExchangesPresenter({loading, error, data}) {
  return (
    <>
      {loading ? (
        <Loader />
      ) : (
        <Container>
          {data.map((exchange, index) => (
            <ul>
              <Li key={index} id={index}>
                {index + 1}. {exchange.name}
              </Li>
              <Div>
                ✅ description :{' '}
                {exchange.description && exchange.description.substring(0, 80)}
                ...
              </Div>
              <Div> ✅ website :{exchange.links && exchange.links.website}</Div>
            </ul>
          ))}
        </Container>
      )}
    </>
  );
}

ExchangesPresenter.propTypes = {
  data: PropTypes.array,
  error: PropTypes.string,
  loading: PropTypes.bool.isRequired,
};

export default ExchangesPresenter;
